/* header bar */
div.app-header {
  padding: 5px 0;
  min-width: 320px;
  height: 40px;
  background-color: black;
  border-bottom: 1px solid #4f0404;
  font-family: "Lato", Helvetica, Arial, sans-serif;
  color: white; }

/* header title */
div.header-title {
  float: left; }
  div.header-title h2.title {
    margin: 7px 15px;
    /* text styles */
    font-size: 21px;
    line-height: 25px;
    letter-spacing: 2px; }
    div.header-title h2.title a {
      color: white;
      text-decoration: none; }
      div.header-title h2.title a:visited {
        color: white; }
      div.header-title h2.title a:hover {
        color: #f64242; }

/* navigation menu */
div.header-nav {
  margin: 7px 15px; }
  div.header-nav a.navigation.nav-link {
    margin-right: 15px;
    /* text styles */
    text-transform: uppercase;
    text-decoration: none;
    line-height: 25px;
    letter-spacing: 1px;
    font-weight: 300;
    color: white; }
    div.header-nav a.navigation.nav-link.current {
      color: #c80a0a;
      cursor: default; }
    div.header-nav a.navigation.nav-link:hover, div.header-nav a.navigation.nav-link.current:hover {
      color: #f64242; }

/* desktop styles */
@media screen and (min-width: 768px) {
  div.header-nav {
    float: right; }
  div.app-header h2.title {
    font-weight: 100; } }

/* mobile styles */
@media screen and (max-width: 767px) {
  div.header-nav {
    float: none; }
  div.app-header h2.title {
    display: none; } }

