div.table-container {
  width: 100%; }

/* table */
table.table {
  margin: 7px auto;
  width: 94%;
  min-width: 320px;
  border: 1px solid #333;
  border-collapse: collapse; }

/* table header */
thead.table-header {
  cursor: default; }
  thead.table-header th.table-header {
    margin: 0;
    padding: 8px 4px;
    font-size: 13px;
    background-color: #707070;
    border: 1px solid #333 !important;
    /* text styles */
    text-transform: capitalize;
    color: black; }
    thead.table-header th.table-header.sortable {
      cursor: pointer; }
      thead.table-header th.table-header.sortable:hover {
        background-color: #5e5e5e; }

/* table body */
tbody.table-body tr.odd-row {
  background-color: #f2f2f2; }

tbody.table-body tr.even-row {
  background-color: #d9d9d9; }

tbody.table-body tr:hover {
  cursor: default;
  background-color: #fcbcbc; }

tbody.table-body td.table-cell {
  margin: 0;
  padding: 8px 4px;
  font-size: 13px;
  border-left: 1px solid #525252;
  border-right: 1px solid #525252; }
  tbody.table-body td.table-cell.left-end {
    border-left-color: #333; }
  tbody.table-body td.table-cell.right-end {
    border-right-color: #333; }



